.modal {
  width: 728px;
  height: 444px;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  background: no-repeat url("../img/modal.jpg");
  background-color: #fbf8f8;
}

.modal-sucсess {
  width: 728px;
  height: 444px;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  background-color: #fbf8f8;
}

.modal__hide {
  display: none;
}

.modal-success--hide {
  display: none;
}

.modal__show,
.modal-success__show {
  display: block;
}

.modal__button {
  box-sizing: border-box;
  width: 175px;
  height: 50px;
  border: none;
  margin: 0;
  padding: 17px 40px;
  margin-left: 47px;
}

.modal__header,
.modal-sucess__header {
  position: relative;
  font-family: "Montserrat", "Verdana", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  line-height: 43px;
  color: #141517;
  margin: 0;
  padding: 0;
  margin-bottom: 55px;
  margin-left: 60px;
}

.modal__form input {
  box-sizing: border-box;
  width: 269px;
  height: 50px;
  border: 1px solid rgba(44, 46, 63, 0.3);
  padding: 15px;
  margin-bottom: 21px;

  &:hover {
    border: 1px solid rgba(44, 46, 63, 0.6);
  }
}

.modal__wrapper {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  width: 390px;
  height: 100%;
  margin-left: auto;
  padding-top: 77px;
  padding-left: 60px;
}

.modal-sucсess__wrapper {
  box-sizing: border-box;
  width: 399px;
  height: 100%;
  margin: 0 auto;
  padding-top: 179px;
}

.modal__label {
  position: relative;
}

.modal__required {
  position: absolute;
  color: #fe7865;
  top: -8px;
  left: 11px;
}

.modal__form .modal__mobile {
  padding-left: 23px;
}

.modal__close,
.modal-sucсess__close {
  position: absolute;
  width: 22px;
  height: 22px;
  top: 20px;
  right: 20px;
  background: 0 0;
  border: 0;
  cursor: pointer;
  margin: 0;
  padding: 0;

  &::after {
    content: "";
    position: absolute;
    top: 10;
    right: 0;
    width: 22px;
    height: 1px;
    background-color: rgba(20, 21, 23, 0.2);
    transform: rotate(45deg);
  }

  &::before {
    content: "";
    position: absolute;
    top: 10;
    right: 0;
    width: 22px;
    height: 1px;
    background-color: rgba(20, 21, 23, 0.2);
    transform: rotate(-45deg);
  }
}

@media (max-width: 767px) {
  .modal {
    // display: none;
    width: 320px;
    height: 568px;
    background: none;
    background-color: #ffffff;
    bottom: 0;
    right: 0;
    margin: auto;
  }

  .modal__wrapper {
    width: 320px;
    padding: 0;
    padding-top: 100px;
    padding-left: 16px;
    padding-right: 16px;
  }

  .modal__header {
    text-align: center;
    padding: 0;
    margin: 0;
    margin-bottom: 50px;
  }

  .modal__form input {
    width: 288px;
    margin-bottom: 20px;
  }

  .modal__button {
    width: 288px;
    margin: 0 auto;
    font-size: 10px;
    line-height: 12px;
    font-weight: 500;
    padding-left: 103px;
    padding-right: 103px;
  }

  .modal-sucсess {
    width: 320px;
    height: 568px;
  }

  .modal-sucсess__wrapper {
    width: auto;
  }

  .modal-sucсess__header {
    text-align: center;
  }
}
